import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { FinanceComponent } from './finance.component';

const routes: Routes = [
  { path: '', component: FinanceComponent },
  {
    path: 'compound-interest',
    loadChildren: () =>
      import('./compound-interest/compound-interest.module').then(
        m => m.CompoundInterestModule
      )
  },
  { path: 'retirement-calculator', loadChildren: () => import('./retirement-calculator/retirement-calculator.module').then(m => m.RetirementCalculatorModule) }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class FinanceRoutingModule {}
